// app/collections/men-all-shoes/page.js

import ProductCard from '../../../components/ProductCard';

const BASE_URL = 'https://striking-miracle-75163e8551.strapiapp.com';

export async function getMenProducts() {
    const res = await fetch(`${BASE_URL}/api/products?populate=*&filters[category][name][$eq]=Men`);

    if (!res.ok) {
        throw new Error('Failed to fetch men products');
    }

    const data = await res.json(); // Parse the JSON response

    console.log(data.data); // Products array
    return data.data;
}



export default async function MenAllShoesPage() {
    const products = await getMenProducts();

    return (
        <div className="lg:px-6">
            <h1 className="text-2xl font-bold px-4 pt-4">Men's Shoes</h1>
            <div className="grid grid-cols-2 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-2 p-4">
                {products.map((p) => (
                    <ProductCard
                        key={p.id}
                        product={{
                            ...p,
                            id: p.id,
                            images: p.images || []
                        }}
                    />
                ))}
            </div>
        </div>
    );
}
